const Movie = r => require.ensure(
  [],
  () => r(require('@/components/movie/index')),
  'Movie'
)
const FilmDetail = r => require.ensure(
  [],
  () => r(require('@/components/movie/film/detail')),
  'FilmDetail'
)
const Search = r => require.ensure(
  [],
  () => r(require('@/components/movie/search')),
  'Search'
)
const MovieImage = r => require.ensure(
  [],
  () => r(require('@/components/movie/common/movie_image')),
  'MovieImage'
)
const MovieComment = r => require.ensure(
  [],
  () => r(require('@/components/movie/common/movie_comment')),
  'MovieComment'
)
const Subscribe = r => require.ensure(
  [],
  () => r(require('@/components/movie/common/movie_subscribe')),
  'Subscribe'
)
const MovieRecommend = r => require.ensure(
  [],
  () => r(require('@/components/movie/common/recommendList')),
  'Subscribe'
)
const MovieDecode = r => require.ensure(
  [],
  () => r(require('@/components/movie/movie_decode')),
  'MovieDecode'
)
const MovieDetail = r => require.ensure(
  [],
  () => r(require('@/components/movie/common/wechat-detail')),
  'MovieDetail'
)

export default [{
  path: '/movie',
  component: Movie,
  name: 'Movie',
  meta: {
    title: '-',
    keepAlive: true,
    reload_address: ''
  }
}, {
  path: '/film/detail/:id',
  component: FilmDetail,
  name: 'FilmDetail',
  meta: {
    title: '',
    keepAlive: true,
    reload_address: ''
  }
}, {
  path: '/movie/search',
  component: Search,
  name: 'Search',
  meta: {
    title: '',
    keepAlive: true,
    reload_address: ''
  }
}, {
  path: '/movie/image',
  component: MovieImage,
  name: 'MovieImage',
  meta: {
    title: '',
    keepAlive: false,
    reload_address: ''
  }
}, {
  path: '/movie_comment',
  component: MovieComment,
  name: 'MovieComment',
  meta: {
    title: '',
    keepAlive: false,
    reload_address: ''
  }
}, {
  path: '/subscribe',
  component: Subscribe,
  name: 'Subscribe',
  meta: {
    title: '',
    keepAlive: false,
    reload_address: ''
  }
}, {
  path: '/movie/recommend/:id',
  component: MovieRecommend,
  name: 'MovieRecommend',
  meta: {
    title: '&',
    keepAlive: false,
    reload_address: ''
  }
}, {
  path: '/movie_decode',
  component: MovieDecode,
  name: 'MovieDecode',
  meta: {
    title: 'VIP'
  }
}, {
  path: '/movie/detail',
  component: MovieDetail,
  meta: {
    keepAlive: false,
    title: ''
  }
}]



