/**
 * @method 
 * @param {string} key key
 * @param {string} val  null
 */
export const cache = (key, val) => {
  if (val || val === null || val === 0) {
    val === null ? localStorage.removeItem(key) : localStorage.setItem(key, val)
  } else {
    return localStorage[key]
  }
}

/**
 *
 * @param {date} time 
 * @returns {string} 
 */
export const timeFormat = (time) => {
  const newTime = new Date() // 
  const oldTime = new Date(time) // 
  const timeDifference = newTime.getTime() - oldTime.getTime() // 
  const yesterdayTime = newTime.getTime() - (60 * 60 * 1000 * 24) // 
  const yesterday = new Date(yesterdayTime).getDate() // 
  if (timeDifference < (60 * 1000)) {
    return ''
  } else if (timeDifference < (60 * 60 * 1000)) {
    let txt = Math.floor(timeDifference / 60000)
    return txt + ''
  } else if (timeDifference < (60 * 60 * 1000 * 24)) {
    let txt = Math.floor(timeDifference / 3600000)
    return txt + ''
  } else if (timeDifference > (60 * 60 * 1000 * 24) && oldTime.getDate() === yesterday) {
    let txt = timestampToStr(time, 'h:m')
    return ' ' + txt
  } else {
    return timestampToStr(time, 'M-D h:m')
  }
}

/**
 * @method YMDhms
 * @param {number} time 
 * @param {string} str Y M  D h m s
 */
export const timestampToStr = (time, str) => {
  const date = new Date(time)
  str = str.replace('Y', date.getFullYear())
  str = str.replace('M', (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1))
  str = str.replace('D', (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()))
  str = str.replace('h', (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()))
  str = str.replace('m', (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()))
  str = str.replace('s', (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()))
  return str
}

