/**
 * @param { Object } el vuethis
 */
export const setLogin = (el) => {
  let codeLength = strTimes(location.href, 'code')
  if (codeLength === 1 && location.href.toLocaleLowerCase().indexOf('invitecode') === -1) {
    let stateParams = getParams(location.search.substr(1, location.search.length - 1))
    let str = JSON.parse(Base64.decode(stateParams.state))
    let data = {
      code: stateParams.code,
      type: 1
    }
    data = Object.assign(data, registerInfo())
    // 
    if (str.inviteCode) {
      data.invitation = str.inviteCode
    //  
    } else if (str.invitecode) {
      data.invitation = str.invitecode
    }
    data.origin_url = location.origin
    $api.post('WECHAT_LOGIN', data, (res) => {
      cache('uid', res.data.uid)
      cache('user_token', res.data.user_token)
      cache('nickname', res.data.nickname)
      cache('first_login', res.data.first_login)
      cache('user_icon', res.data.avatar)
      cache('inviteCode', res.data.invitation)
      cache('userInfo', JSON.stringify(res.data))
      if (res.data.unionid) {
        cache('unionid', res.data.unionid)
      }
      cache('handOperated', null) // 
      cache('showLoginSuccess', true)
      el.$store.commit('common_login_bol', true)
      el.$store.commit('common_set_userInfo', res.data)
      $api.get('MY_CHANNEL', {}, (res) => {
        el.$store.common_get_channel(res.data, 'my_channel')
      })
      $api.get('RECOND_CHANNEL', {}, (res) => {
        el.$store.common_get_recondChannel(res.data, 'recond_channel')
      })
      if (str.path) {
        let url = location.origin + '/#' + decodeURIComponent(str.path)
        // 
        if (str.inviteCode) {
          url = url + '?inviteCode=' + str.inviteCode
        }
        // 
        if (str.call) {
          if (url.indexOf('?') > -1) {
            url = url + `&call=${str.call}`
          } else {
            url = url + `?call=${str.call}`
          }
        }
        // id
        if (str.gid) {
          if (url.indexOf('?') > -1) {
            url = url + `&gid=${str.gid}`
          } else {
            url = url + `?gid=${str.gid}`
          }
        }
        location.href = url
      } else {
        location.href = location.origin
      }
    })
  }
}
