/**
 *
 * @param {object} e
 */
export const isImg = (e) => {
  if (typeof FileReader !== 'undefined') {
    let file = e.target.files[0]
    if ((file.type).indexOf('image/') === -1) {
      return false
    }
  } else {
    let fileName = e.target.value
    let suffixIndex = fileName.lastIndexOf('.')
    let suffix = fileName.substring(suffixIndex + 1).toUpperCase()
    if (suffix !== 'BMP' && suffix !== 'JPG' && suffix !== 'JPEG' && suffix !== 'PNG' && suffix !== 'GIF') {
      return false
    }
  }
  return true
}

/**
 *
 * @param {number} num 1Ww
 */
export const thousandRead = (num, length, text = 'w') => {
  num = num || 0
  let long = length || 2
  if (num > 9999) {
    return (num / 10000).toFixed(long) + text
  } else {
    return parseInt((num * 1))
  }
}

/**
 * @param {number} type 12
 * @param {number} mid id
 */
// add_attention       key
// cancel_attention    key
export const refreshAttention = (type, mid) => {
  let addArr = cache('add_attention') ? JSON.parse(cache('add_attention')) : [] // 
  let cancelArr = cache('cancel_attention') ? JSON.parse(cache('cancel_attention')) : [] // 
  switch (type) {
    case 1:
      addArr.push(mid)
      cache('add_attention', JSON.stringify(addArr))
      for (let i in cancelArr) {
        if (cancelArr[i] === mid) {
          cancelArr.splice(i, 1)
          cache('cancel_attention', JSON.stringify(cancelArr))
          break
        }
      }
      break
    case 2:
      cancelArr.push(mid)
      cache('cancel_attention', JSON.stringify(cancelArr))
      for (let i in addArr) {
        if (addArr[i] === mid) {
          addArr.splice(i, 1)
          cache('add_attention', JSON.stringify(addArr))
          break
        }
      }
      break
    default:
      break
  }
}
export const uploadAttach = (data) => {
  let contentLen = Math.round(data.file.size * 100 / 1024) / 100
  let fd = new FormData()
  fd.append('Content-Length', contentLen)
  fd.append('OSSAccessKeyId', data.OSSAccessKeyId)
  fd.append('policy', data.policy)
  fd.append('signature', data.signature)
  // 
  fd.append('key', data.path + data.name)
  fd.append('dir', data.path)
  fd.append('success_action_status', '200')
  // 
  fd.append('file', data.file)
  return new Promise((resolve, reject) => {
    axios.post(data.imgServer, fd, {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      transformRequest: (data) => {
        return data
      }
    }).then(res => {
      resolve(res)
    }).catch(err => {
      reject(err)
    })
  })
}
/**
 * 
 * @param {String} usertoken token
 * @param {String} dir 
 * @param {Object} file 
 * @param {String} fileName  // 
 */
export const uploader = (usertoken, dir, file, fileName = new Date().getTime()) => {
  return new Promise((resolve, reject) => {
    $api.get('OSS', {
      user_token: usertoken,
      dir
    }, (res) => {
      let data = {}
      data.imgServer = res.data.host
      data.OSSAccessKeyId = res.data.accessid
      data.policy = res.data.policy
      data.signature = res.data.signature
      data.path = res.data.dir
      let format = file.name.substring(file.name.lastIndexOf('.', file.name.length - 1))
      fileName = fileName + format
      data.name = fileName
      data.file = file
      uploadAttach(data).then(res => {
        resolve({
          res: res,
          url: data.imgServer + '/' + data.path + data.name
        })
      })
    })
  })
}

// 
export const getClientHeight = () => {
  let clientHeight = 0
  if (document.body.clientHeight && document.documentElement.clientHeight) {
    clientHeight = (document.body.clientHeight < document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
  } else {
    clientHeight = (document.body.clientHeight > document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
  }
  return clientHeight
}
// 
export const getDevideWidth = () => {
  return document.documentElement.clientWidth || document.body.clientWidth
}

/**
 * 
 * @param {Object} el dom 
 */
export const getScrollTop = (el) => {
  let scrollTop = 0
  if (el) {
    scrollTop = el.scrollTop
  } else {
    if (document.documentElement && document.documentElement.scrollTop) {
      scrollTop = document.documentElement.scrollTop
    } else if (document.body) {
      scrollTop = document.body.scrollTop
    }
  }
  return scrollTop
}

// 
export const getScrollHeight = () => {
  return Math.max(document.body.scrollHeight, document.documentElement.scrollHeight)
}
