export const exceptWebp = (str, isDouban) => {
  if (typeof str === 'string') {
    let length = str.length
    if (str.substring(length - 5, length) === '.webp') {
      if (isDouban) {
        return str.replace('.webp', '.jpg')
      } else {
        return str.substring(0, length - 5)
      }
    } else {
      return str
    }
  } else {
    return str
  }
}
