/**
 * 
 * @param {number} startTime
 * @param {number} expiredTime
 */
export const timeDifference = (startTime, expiredTime) => {
  if (startTime >= expiredTime) {
    return {
      num: [0, 0, 0, 0],
      string: ['0', '0', '0', '0']
    }
  } else {
    let cha = expiredTime - startTime
    let remainingTime = cha % 86400000
    let remainingHours = remainingTime % 3600000
    let days = parseInt(cha / 86400000)
    let hours = parseInt(remainingTime / 3600000)
    let minute = parseInt(remainingHours / 60000)
    let second = parseInt((remainingHours % 60000) / 1000)
    days = days < 10 ? '0' + days : days
    hours = hours < 10 ? '0' + hours : hours
    minute = minute < 10 ? '0' + minute : minute
    second = second < 10 ? '0' + second : second
    return {
      num: [days, hours, minute, second],
      string: [`${days}`, `${hours}`, `${minute}`, `${second}`]
    }
  }
}
