export const getTimeText = (seconds) => {
  let hours = parseInt(seconds / 3600)
  let minute = parseInt((seconds - 3600 * hours) / 60)
  let second = (seconds) % 60
  return [hours, minute, second].map(v => {
    return v < 10 ? '0' + v : v
  }).join(':')
}
