export  const  dataURLtoFile = (dataurl, filename) => {
  let arr = dataurl.split(',')
  let  mime = arr[0].match(/:(.*?);/)[1]
  filename = filename + '.' +  mime.split('/')[1]
  let  bstr = atob(arr[1])
  let  n = bstr.length
  let  u8arr = new Uint8Array(n)

  while (n--) {
    u8arr[n] = bstr.charCodeAt(n)
  }
  return new File([u8arr], filename, { type: mime })
}
/**
 * 
 * @param {Number} deadTime 
 * @returns {Array}  [day, hour, minute, second]
 */
export const getCountText = (deadTime) => {
  if (getTimeStamp() < deadTime) {
    let seconds = deadTime - getTimeStamp()
    let day = parseInt(seconds / 3600 / 24)
    let hours = parseInt((seconds - day * 3600 * 24) / 3600)
    let minute = parseInt((seconds - day * 3600 * 24 - hours * 3600) / 60)
    let second = seconds % 60
    return [day, hours, minute, second].map(res => {
      return res < 10 ? '0' + res : res
    })
  } else {
    return 0
  }
}
