/**
 * 
 * @param {Object} el dom
 * @returns {Number} 
 */
export const getElPotion = (el) => {
  let actualTop = el.offsetTop
  let current = el.offsetParent
  while (current !== null) {
    actualTop += current.offsetTop
    current = current.offsetParent
  }
  return actualTop
}
/**
 * 
 * @param {Object} el dom
 * @returns {Number} 
 */
export const getElLeftPotion = (el) => {
  let actualTop = el.offsetLeft
  let current = el.offsetParent
  while (current !== null) {
    actualTop += current.offsetLeft
    current = current.offsetParent
  }

  return actualTop
}
