/**
 * 
 * @param {String} prop 
 * @param {String} flag 1  
 * @returns {Array} 
 */
export const compare = (prop, flag) => {
  return function (obj1, obj2) {
    let val1 = Number(obj1[prop])
    let val2 = Number(obj2[prop])
    if (val1 < val2) {
      return flag === 1 ? -1 : 1
    } else if (val1 > val2) {
      return flag === 1 ? 1 : -1
    } else {
      return 0
    }
  }
}
