export const getCoinValue = (str) => {
  let reg = /(\+\d+)/
  if (typeof str !== 'string') {
    return str
  } else {
    let matchStr = str.match(reg)[1]
    return str.replace(matchStr, `<span class="coinColor">${matchStr}</span>`)
  }
}

export const registerInfo = () => {
  let us = navigator.userAgent.toLocaleLowerCase()
  let ua = us.toLocaleLowerCase()
  let obj = {
    os: 'other',
    useragent: us,
    channel: 'browser'
  }
  if (config.source_style === 6) {
    obj.os = 'ios'
    obj.channel = 'ios'
  } else if (config.source_style === 7) {
    obj.os = 'android'
    obj.channel = 'android'
  } else {
    if (isAndroid()) {
      obj.os = 'android'
      if (isWx()) {
        obj.channel = 'weixin'
      } else if (isQQ()) {
        obj.channel = 'qq'
      } else {
        obj.channel = 'browser'
      }
    } else if (isIos()) {
      obj.os = 'ios'
      if (isWx()) {
        obj.channel = 'weixin'
      } else if (isQQ()) {
        obj.channel = 'qq'
      } else {
        obj.channel = 'browser'
      }
    } else {
      obj.channel = 'browser'
      if (ua.indexOf('windows') > -1) {
        obj.os = 'windows'
      } else if (ua.indexOf('mac') > -1 && ua.indexOf('mobile') === '-1') {
        obj.os = 'mac'
        obj.channel = 'browser'
      } else {
        obj.os = 'other'
        obj.channel = 'browser'
      }
    }
  }
  return obj
}
