const uniqueNum = (prefix) => {
  return (prefix || '') + new Date().getTime().toString(36) + Math.random().toString(36).slice(2)
}
let variable = {  // 
  vsn: '/v1',
  source_style: 6,
  package_app_env: '0',
  package_brower: '1',
  product: true,
  ch_rand: uniqueNum(),
  version: '1.8.1',
  devApi: 'http://dev.api.juejinchain.cn', // api http://juejin.wechat-app.club
  // devApi: 'http://.m.juejinchain.com', // api http://juejin.wechat-app.club
  // devApi: 'http://192.168.0.21:8002', // api http://juejin.wechat-app.club
  // productApi: 'http://api.juejinchain.com', // api
  productApi: 'http://api.juejinchain.com', // api
  currentApi: 'http://dev.api.juejinchain.cn', // api
  // currentApi: 'http://api.juejinchain.com', // api http://192.168.0.18:8002/v1/passport/captcha?timestamp=1545642504578
  // currentApi: 'http://192.168.0.18:8002',
  devWebUrl: 'http://dev.wechat.juejinchain.cn', // 
  productWebUrl: 'http://wechat.jjbnews.top', // 
  currentWebUrl: 'http://wechat.jjbnews.top', // 
  packageTime: '',
  downloadUrl: 'http://wechat.jjbnews.top/download/'
}

if (process.env.NODE_ENV === 'pcEnv') {   // 
  // pc
  variable.source_style = 1
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'mobileEnv') {
  // Mobile
  variable.source_style = 2
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'wechatEnv') {
  // 
  variable.source_style = 3
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'appletsEnv') {
  // 
  variable.source_style = 4
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'promotionEnv') {
  // 
  variable.source_style = 5
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'iosEnv') {
  // IOS
  variable.source_style = 6
  variable.package_app_env = '1'
  variable.package_brower = '0'
  variable.product = false
  variable.currentApi = variable.devApi
  variable.currentWebUrl = variable.devWebUrl
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'androidEnv') {
  // 
  variable.source_style = 7
  variable.package_app_env = '1'
  variable.package_brower = '0'
  variable.product = false
  variable.currentApi = variable.devApi
  variable.currentWebUrl = variable.devWebUrl
  variable.packageTime = new Date()
  // iOS
} else if (process.env.NODE_ENV === 'iosproductEnv') {
  variable.source_style = 6
  variable.package_app_env = '1'
  variable.package_brower = '0'
  variable.product = true
  variable.currentApi = variable.productApi
  variable.currentWebUrl = variable.productWebUrl
  // 
} else if (process.env.NODE_ENV === 'androidproductEnv') {
  variable.source_style = 7
  variable.package_app_env = '1'
  variable.package_brower = '0'
  variable.product = true
  variable.currentApi = variable.productApi
  variable.currentWebUrl = variable.productWebUrl
} else {
  // 
  variable.product = false
  if (variable.source_style < 6) {
    variable.currentApi = '/api'
  }
}
export default {
  vsn: variable.vsn,         // 
  /* [
       '1' => 'PC',
       '2' => 'Mobile',
       '3' => '',
       '4' => '',
       '5' => '',
       '6' => 'IOS',
       '7' => '',
     ] */
  source_style: variable.source_style,    // 
  // APPpackage_app_env10APPpackage_brower10
  package_app_env: variable.package_app_env, // APP
  package_brower: variable.package_brower, // 
  product: variable.product, // true false
  ch_rand: variable.ch_rand,  // 
  devApi: variable.devApi, // api
  productApi: variable.productApi, // api
  currentApi: variable.currentApi, // api
  devWebUrl: variable.devWebUrl, // 
  productWebUrl: variable.productWebUrl, // 
  currentWebUrl: variable.currentWebUrl, // app
  downloadUrl: variable.downloadUrl,
  // androidDownloadUrl: 'https://sj.qq.com/myapp/detail.htm?apkName=com.juejinchain.android', // 
  androidDownloadUrl: variable.downloadUrl, // 
  iOSDownloadUrl: 'https://sj.qq.com/myapp/detail.htm?apkName=com.juejinchain.android', // iOS
  version: variable.version,
  packageTime: variable.packageTime
}



