import Vue from 'vue'
import Router from 'vue-router'
import secondRouter from './second'
import movie from './movie'
// 
// const HomeIndex = r => require.ensure(
//   [],
//   () => r(require('@/components/home/index')),
//   'chunkname1'
// )
// 
const ArticleDetails = r => require.ensure(
  [],
  () => r(require('@/components/home/article-details')),
  'ArticleDetails'
)
// 
const LocalDetails = r => require.ensure(
  [],
  () => r(require('@/components/indexList/local-details')),
  'LocalDetails'
)
// 404
const notFound = r => require.ensure(
  [],
  () => r(require('@/components/common/not-found')),
  'notFound'
)
// 
const PersonalCenter = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/index')),
  'chunkname2'
)
// -
const FocusCenter = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/focus-center')),
  'chunkname3'
)
// --
const MediaNumber = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/media-number')),
  'chunkname3'
)
// -
const Collection = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/collection')),
  'chunkname4'
)
// -
const Historys = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/history')),
  'chunkname5'
)
// -
const AwardFeedback = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/award-feedback')),
  'chunkname6'
)
// -
const AboutUs = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/about-us')),
  'chunkname7'
)

// 
const SelectCity = r => require.ensure(
  [],
  () => r(require('@/components/common/select-city')),
  'chunkname1'
)

// dome
const Dome = r => require.ensure(
  [],
  () => r(require('@/components/common/dome')),
  'Dome'
)
// 
const richIndex = r => require.ensure(
  [],
  () => r(require('@/components/richPlay/richIndex')),
  'richIndex'
)

// 
const RichList = r => require.ensure(
  [],
  () => r(require('@/components/richPlay/richList')),
  'RichList'
)

// 
const BurstRich = r => require.ensure(
  [],
  () => r(require('@/components/burst_rich/index')),
  'BurstRich'
)
// 
const AwardPag = r => require.ensure(
  [],
  () => r(require('@/components/awardPag/index')),
  'AwardPag'
)
// 
const CarDetail = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/car_detail/index')),
  'CarDetail'
)
// 
const GoodsDetail = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/goods_detail/index')),
  'GoodsDetail'
)
// 
const HouseDetail = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/house_detail/index')),
  'HouseDetail'
)

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/Dome',
    name: 'Dome',
    component: Dome
  },
  // 
  // {
  //   path: '/',
  //   name: 'HomeIndex',
  //   component: HomeIndex,
  //   meta: {
  //     reload_address: '',      // 
  //     keepAlive: true,         // 
  //     title: '-',
  //     bgcolor: '#ffaa00'
  //     // title: ''
  //   }
  // },
  // 
  {
    path: '/SelectCity',
    name: 'SelectCity',
    component: SelectCity,
    meta: {
      reload_address: '',      // 
      keepAlive: false,         // 
      title: ''
    }
  },
  // 
  {
    path: '/ArticleDetails/:aid',
    name: 'ArticleDetails',
    component: ArticleDetails,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: ''
    }
  },
  // 404
  {
    path: '/notFound',
    name: 'notFound',
    component: notFound,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: ''
    }
  },
  // 
  {
    path: '/LocalDetails/:aid',
    name: 'LocalDetails',
    component: LocalDetails,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: ''
    }
  },
  // 
  {
    path: '/personal_center',
    name: 'PersonalCenter',
    component: PersonalCenter,
    meta: {
      reload_address: '',
      keepAlive: true,
      title: '-',
      bgcolor: '#ff9b00'
    }
  },
  // -
  {
    path: '/FocusCenter',
    name: 'FocusCenter',
    component: FocusCenter,
    meta: {
      reload_address: '/personal_center',
      keepAlive: false,
      title: '',
      bgcolor: '#ffaa00'
    }
  },
  // --
  {
    path: '/MediaNumber/:mid',
    name: 'MediaNumber',
    component: MediaNumber,
    meta: {
      reload_address: '/personal_center',
      keepAlive: false,
      title: '',
      bgcolor: '#ffaa00'
    }
  },
  // -
  {
    path: '/Collection',
    name: 'Collection',
    component: Collection,
    meta: {
      reload_address: '/personal_center',
      keepAlive: false,
      title: '',
      bgcolor: '#ffaa00'
    }
  },
  // -
  {
    path: '/Historys',
    name: 'Historys',
    component: Historys,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '',
      bgcolor: '#ffaa00'
    }
  },
  // -
  {
    path: '/AwardFeedback',
    name: 'AwardFeedback',
    component: AwardFeedback,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '',
      bgcolor: '#ffaa00'
    }
  },
  // -
  {
    path: '/AboutUs',
    name: 'AboutUs',
    component: AboutUs,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '',
      bgcolor: '#ffaa00'
    }
  },
  // 
  {
    path: '/rich_list',
    name: 'RichList',
    component: RichList,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: ''
    }
  },
  // 
  {
    path: '/richIndex',
    name: 'richIndex',
    component: richIndex,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: ''
    }
  },
  // 
  {
    path: '/burst_rich',
    name: 'BurstRich',
    component: BurstRich,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: ''
    }
  },
    // 
  {
    path: '/prize',
    name: 'AwardPag',
    component: AwardPag,
    meta: {
      reload_address: '',
      keepAlive: true,
      title: ''
    }
  },
    // 
  {
    path: '/car_detail/:id',
    name: 'CarDetail',
    component: CarDetail,
    meta: {
      reload_address: '',
      keepAlive: true,
      title: ''
    }
  },
    // 
  {
    path: '/goods_detail/:id',
    name: 'GoodsDetail',
    component: GoodsDetail,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: ''
    }
  },
  // 
  {
    path: '/house_detail/:id',
    name: 'HouseDetail',
    component: HouseDetail,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: ''
    }
  },
  ...secondRouter,
  ...movie
  ]
})



