// 706 token 
export const tokenFailure = () => {
  $utils.clearCache()
  if ($utils.isWx()) {
    if ($utils.cache('handOperated')) {
      store.commit('common_login_bol', false) // 
      store.commit('common_login_box', true) // 
    } else {
      // 
      store.commit('common_login_box', false)
      store.commit('common_login_bol', false)
      // $utils.wxLogin()
    }
  } else {
    store.commit('common_login_bol', false) // 
    store.commit('common_login_box', true) // 
  }
}
// 
export const OtherLogin = (res) => {
  // app
  if (config.source_style >= 6) {
    $utils.clearCache()
    if ($utils.isWx()) {
      if ($utils.cache('handOperated')) {
        store.commit('common_login_bol', false) // 
        store.commit('common_login_box', true) // 
      } else {
        $utils.wxLogin()
      }
    } else {
      store.commit('common_login_bol', false) // 
      store.commit('common_login_box', true) // 
      store.commit('common_other_login', true)
    }
  } else {
    tokenFailure()
  }
}
// code 3 
export const msgAlert = (res) => {
  store.commit('set_shareSuccessObj', {
    type: 18,
    shareSuccessObj: {msg: res.data.msg}
  })
  store.commit('set_shareType', 18)
  store.commit('setShowShare', true)
}
/**
 * 
 * @param {string} constant url
 * @param {object} data 
 * @param {function} callback 
 * @param {function} errorCallback 0
 * @param {function} catchCallback 
 */
export const get = (constant, data = {}, callback, errorCallback, catchCallback) => {
  data.source_style = config.source_style
  data.uid = $utils.cache('uid') ? $utils.cache('uid') : 0
  data.user_token = $utils.cache('user_token') ? $utils.cache('user_token') : ''
  data.device_type = JSON.parse($utils.cache('phoneModel'))[1]
  data.version = config.version
  let url = apiConstant[constant]
  url = config.currentApi + url
  const timer = setTimeout(() => { // 
    Indicator.close()
    Toast({
      message: ',',
      position: 'middle',
      duration: 1000
    })
  }, 20000)
  axios.get(
    url, {
      params: data
    }
  ).then((res) => {
    // 
    // res = {
    //   data: {
    //     data: {
    //       maintenance_time: '2018-10-30 21:04:04  2018-10-31 21:04:13',
    //       maintenance_info: '1app2'
    //     },
    //     code: 707,
    //     msg: '',
    //     vsn: '1.2.3'
    //   }
    // }
    // 
    // res.data.vsn = '1.2.3'
    // alertcode3
    // res = {
    //   data: {
    //     code: 3,
    //     msg: 'code3'
    //   }
    // }
    // 
    let flag = true
    for (let v of urlArr) {
      if (res.config.url.indexOf(v) > -1) {
        flag = false
        break
      }
    }
    if (flag) {
      Indicator.close()
    }
    clearTimeout(timer) // 
    if ($utils.isUpdate(res.data.vsn ? res.data.vsn : config.version) === 1 && !$utils.cache('isShowUpdate')) {
      store.commit('setShowShare', true)
      store.commit('set_shareType', 8)
    }
    if (res.data.code === 706) {
      tokenFailure()
      return
      // 
    } else if (res.data.code === 705) {
      OtherLogin()
      return
    } else if (res.data.code === 707) {
      // 
      if (res.data.data.maintenance_time) {
        store.commit('common_set_updateData', Object.assign({
          vsn: res.data.vsn
        }, res.data.data))
        router.push('/service_update')
        // location.href = '/#/service_update'
        return
      }
      // 2
    } else if (res.data.code === 2) {
      typeof errorCallback === 'function' && errorCallback(res.data)
      return
    } else if (res.data.code === 3) {
      msgAlert(res)
      return
    } else if (res.data.code === 801 || res.data.code === 802 || res.data.code === 803) {
      if (res.data.code === 802) {
        if (config.source_style >= 6 || $utils.isWx()) {
          typeof errorCallback === 'function' && errorCallback(res.data)
        } else {
          store.commit('setShowShare', true)
          store.commit('set_shareType', 17)
          let shareSuccessObj = {
            
          }
          store.commit('set_shareSuccessObj', {
            type: 17,
            shareSuccessObj
          })
        }
      } else {
        typeof errorCallback === 'function' && errorCallback(res.data)
      }
      return
    } else if (res.data.code !== 0) {
      Toast({
        message: res.data.msg,
        position: 'middle',
        duration: 1000
      })
      typeof errorCallback === 'function' && errorCallback(res.data)
      return
    }
    typeof callback === 'function' && callback(res.data)
  }).catch((error) => {
    typeof catchCallback === 'function' && catchCallback(error)
    Indicator.close()
  })
}

export const post = (constant, data = {}, callback, errorCallback, catchCallback) => {
  let str = `?source_style=${config.source_style}&uid=${$utils.cache('uid') ? $utils.cache('uid') : 0}&user_token=${$utils.cache('user_token') ? $utils.cache('user_token') : ''}&device_type=${JSON.parse($utils.cache('phoneModel'))[1]}&version=${config.version}`
  let url = apiConstant[constant] + str
  url = config.currentApi + url
  Indicator.open()
  const timer = setTimeout(() => { // 
    Indicator.close()
    Toast({
      message: ',',
      position: 'middle',
      duration: 1000
    })
  }, 20000)
  axios.post(
    url,
    qs.stringify(data)
  ).then((res) => {
    clearTimeout(timer) // 
    Indicator.close()
    if ($utils.isUpdate(res.data.vsn ? res.data.vsn : config.version) === 1 && !$utils.cache('isShowUpdate')) {
      store.commit('set_shareType', 8)
      store.commit('setShowShare', true)
    }
    if (res.data.code === 706) {
      tokenFailure()
      return
    } else if (res.data.code === 705) {
      OtherLogin()
      return
    } else if (res.data.code === 2) {
      return typeof errorCallback === 'function' && errorCallback(res.data)
    } else if (res.data.code === 3) {
      return msgAlert(res)
    } else if (res.data.code === 707) {
      // 
      if (res.data.data.maintenance_time) {
        store.commit('common_set_updateData', Object.assign({
          vsn: res.data.vsn
        }, res.data.data))
        router.push('/service_update')
        // location.href = '/#/service_update'
        return
      }
      //  ()
    } else if (res.data.code === 801 || res.data.code === 802 || res.data.code === 803) {
      typeof errorCallback === 'function' && errorCallback(res.data)
      return
      //  ()
    } else if (res.data.code === 802) {
      Toast({
        message: res.data.msg,
        position: 'middle',
        duration: 1000
      })
      typeof errorCallback === 'function' && errorCallback(res.data)
    } else if (res.data.code === 1) {
      if (res.config.url.indexOf('/wechat/oath') === -1) {
        Toast({
          message: res.data.msg,
          position: 'middle',
          duration: 1000
        })
        typeof errorCallback === 'function' && errorCallback(res.data)
      } else {
        if (data.type * 1 === 2) {
          typeof errorCallback === 'function' && errorCallback(res.data)
          Toast({
            message: res.data.msg,
            position: 'middle',
            duration: 1000
          })
        }
      }
      return
    } else if (res.data.code !== 0) {
      Toast({
        message: res.data.msg,
        position: 'middle',
        duration: 1000
      })
      typeof errorCallback === 'function' && errorCallback(res.data)
      return
    }
    typeof callback === 'function' && callback(res.data)
  }).catch((error) => {
    typeof catchCallback === 'function' && catchCallback(error)
    // if (error)
    Toast({
      message: '',
      position: 'middle',
      duration: 1000
    })
    Indicator.close()
  })
}
