/**
 * 
 * @param {url} url url 
 * @param {data} data 
 * @param {successCallback} successCallback 
 * @param {errorCallback} errorCallback 
 */
export const appxhr = (url, data, successCallback, errorCallback) => {
  Indicator.open()
  const timer = setTimeout(() => { // 
    Indicator.close()
    Toast({
      message: ',',
      position: 'middle',
      duration: 1000
    })
  }, 20000)

  function setPlus () {
    var xhr = new window.plus.net.XMLHttpRequest()
    xhr.onreadystatechange = function () {
      if (xhr.readyState === 4) {
        clearTimeout(timer) // 
        Indicator.close()
        if (xhr.status === 200) {
          typeof successCallback === 'function' && successCallback(JSON.parse(xhr.responseText))
        }
        typeof errorCallback === 'function' && errorCallback(JSON.parse(xhr.responseText))
      }
    }
    xhr.open('GET', `${url}?${qs.stringify(data)}`)
    xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded')
    xhr.send(qs.stringify(data))
  }
  if (window.plus) {
    setPlus()
  } else {
    document.addEventListener('plusready', function () {
      setPlus()
    })
  }
}

